import React, { useState } from 'react'
import "./css/css.css"

export default function Main() {
    const [Array, setArray] = useState([])
    const [Text, setText] = useState('')
    const [Edit, setEdit] = useState('')
    const [Toggle, setToggle] = useState(null)

    const submit = (e) => {
        e.preventDefault()
        const info = {
            id: new Date().getTime(),
            text: Text,
        }
        setArray([...Array].concat(info))
        setText('')

    }
    const remove = (id) => {
        setArray([...Array].filter((info)=>info.id !== id))
    }
    const edit = (id) => {
        setArray([...Array].map((info)=>{
            
            if (info.id === id) {
                info.text = Edit
            }
            return info
        }))
        setToggle(null)
        setEdit('')
    }

    return (
        <section>
            <form onSubmit={submit} >
                <input className="input" type="text" value={Text} onChange={(e)=>setText(e.target.value)}  />
                <button className="submit"  type="submit" >Submit</button>
            </form>
            <article className="list" >
                {Array.map((info)=>{
                    const {id,text} = info
                    return (
                        <div className="task" >
                        {Toggle === id ? <input className="text" type="text" value={Edit} onChange={(e)=>setEdit(e.target.value)}  />:<h1 className="text" >{text}</h1>}
                        <button className="remove" onClick={()=>remove(id)} >Remove</button>
                        <button className="edit"  onClick={Toggle === id ? ()=>edit(id) : ()=>setToggle(id)  } >{Toggle === id ? "Edit" : "Submit Edit"}</button>
                        </div>
                    )
                })}
            </article>
        </section>
    )
}
